<template>
    <div class="main__info active">
     <div class="main__info-block" v-if="movie">
         <img :src="getImgFull + movie.backdrop_path" alt="" class="main__info-img">
        <div class="main__info-content">
             <div class="main__info-content-block">
                 <h2 class="main__info-content-title">
                     {{movie.title || movie.name }}
                 </h2>
                 <p class="main__info-content-text">
                     {{movie.overview}}
                 </p>
                 <p class="main__info-content-date">
                     <span>{{new Date(movie.release_date).getFullYear() ||
                         new Date(movie.first_air_date).getFullYear()
                         
                         
                         }},</span>
                     <span v-for="(genres,idx) in movie.genres" :key="genres.id">
                     {{genres.name}}
                     <span v-if="idx < movie.genres.length - 1 " >,</span>
                     </span>
                 </p>
                 
                 <Actors
                     :type="/tv/" :id="movie.id" :count="4"
                     
                     />
 
         
             </div>
         
         </div> 
 
     </div>
   </div>
   </template>
   
   <script>
   import {ref, onBeforeMount} from 'vue';
   import {useRoute} from 'vue-router';
   import { mapGetters } from 'vuex';
     
     import Actors from '@/components/UI/Actors.vue';
 
   export default {
     components:{
         Actors
     },
       setup(){
         const movie = ref({});
         const route = useRoute();
   
       onBeforeMount(() => {
         fetch(`https://api.themoviedb.org/3/tv/${route.params.id}?api_key=ff73598636b772c5fca88d178c32f1a4&language=ru-RU`)
         .then(response => response.json())
         .then(data => {
           movie.value = data;
           console.log(data);
         })
       });
       return{
         movie
       }
       },
       computed: {
     ...mapGetters(['getImgFull', 'getImg'])
   },
   
   }
 
 
   </script>